






































export default {
	data() {
		return {
			phone: '',
			password: '',
			positionTop: 0,
			showClearIcon: false,
			inputClearValue: "",
			showPassword: true,
		};
	},
	methods: {
		goWechatLogin() {
			uni.navigateTo({
				url: '../login-wechat/login-wechat'
			})
		},
		initPosition() {
			/**
			* 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			* 反向使用 top 进行定位，可以避免此问题。
			*/
			this.positionTop = uni.getSystemInfoSync().windowHeight - 70;
		},
		bindClearInput: function (e) {
			this.inputClearValue = e.target.value;
			if (e.target.value.length > 0) {
				this.showClearIcon = true;
			} else {
				this.showClearIcon = false;
			}
		},
		clearIcon: function () {
			this.inputClearValue = "";
			this.showClearIcon = false;
		},
		changePassword: function () {
			this.showPassword = !this.showPassword;
		},
		login() {
			var _this = this
			console.log(this.phone)
			console.log(this.password)
			uni.request({
				url: 'http://www.aikm.cn/api/login',
				method: 'POST',
				data: {
					judge: 1,
					phone: _this.phone,
					password: _this.password
				},
				success: res => {
					if(res.data.code == 1) {
						if(!res.data.data.head_img) {
							res.data.data.head_img = '/static/images/head_img.png'
						}
						console.log(JSON.stringify(res.data))
						uni.setStorage({
							key: 'userData',
							data: res.data.data,
							success: function () {
								// console.log('success');
							}
						});
						uni.setStorage({
							key: 'hasLogin',
							data: true,
							success: function () {
								// console.log('success');
							}
						});
						uni.switchTab({
							url: '../../index/index',
						});
					} else {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon: 'none',
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onLoad() {
		this.initPosition();
	}
}



